

/* Instruction Button */
a{
  text-decoration: none;
  color: #A1E8AF;
}

a:hover{
  color: #f1f1f1;
}

/* Green SideNav Box */
.sidenav {
  position: absolute;
  height: 100vh;
  width: 190px;
  top: 0;
  left: 0;
  background-color: #A1E8AF;
  opacity: .3;
  padding-top: 20px;
}


.conversation{
  background-color:white;
  margin-bottom: 20px;
  height: 100vh;
  overflow: scroll;
  margin-right: 35px;
  padding-top:75px;
}

ul{
  margin-top: 10px;
  margin-left: 3px;
  padding-bottom: 3px;
}


.User{
  text-align: left;
  list-style: none;
  background-color:#f1f1f1;
  color:#453F3C;
  border: 2px solid #C3BABA;
  background-color: #C2C2C2;
  border-radius: 15px;
  padding: 10px;
  margin-top: 70px;
  margin-left: 75px;
  margin-right: 35px;
}

.Boss{
  list-style: none;
  color:#f1f1f1;
  border: 2px solid #dedede;
  background-color: #495057;
  border-radius: 15px;
  padding: 10px;
  margin-top: 70px;
  margin-right: 35px;
  font-style: bold;
}

.Hintbot{
  list-style-type: none;
  list-style: none;
  color:#818181;
  border: 2px solid #A1E8AF;
  background-color: #f1f1f1;
  border-radius: 15px;
  padding: 10px;
  margin-top: 10px;
  margin-right: 35px;
  font-style: italic;
}

/* User Textbox */
.footer{
  margin-top:15px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
  font-family: 'Lucida Sans', Verdana, sans-serif;
}

/* This applies from 0px to 600px */
@media all and (max-width: 600px) {
  .sidenav {
    display: none;
  }
  .input-group{
    display: flex;
    flex-direction:column;
    width:100%;
  }
  .conversation{
    padding-top: 300px;
    position: fixed;
    bottom: 100px;
  }

  div.footer > div > textarea{
    width: 100% !important;
  }

  .navbar{
    display: flex;
    flex-direction: column;
  }
}

@media all and (min-width: 600px) {
  .conversation{
    margin-left: 190px;
    width: 100vh;
  }

  .my-nav-button{
    position: absolute;
    right: 10px;
  }
}
